<template>
  <div class="ma-2 mx-0 yb-type-more-project-container">
    <v-tabs v-model="select" @change="change">
      <v-tab v-for="(i,index) in item" :key="index">
        {{ i.name }}
      </v-tab>
    </v-tabs>
    <ul class="infinite-list pb-4 pl-0 mt-2" v-infinite-scroll="load" style="overflow:auto" :infinite-scroll-disabled="disable" :infinite-scroll-distance="50" :infinite-scroll-delay="500">
      <XcProject v-for="(item,index) in data" :data="controlShowDivider(item, index)" :key="index"
                 @onClick="()=>{$router.push('detail?title='+item.name+'&id='+item.id);}" type="search"/>
    </ul>
  </div>
</template>

<script>
import XcProject from "@/components/project";
import Http from '@/repository/http';
import {InfiniteScroll} from "element-ui";
import Vue from "vue";
Vue.use(InfiniteScroll)

export default {
  name: "type_more",
  data: () => ({
    disable: false,
    data: [],
    select: 1,
    item: [{
      status: 0,
      name: ''
    },
      {
        status: 1,
        name: ''
      },
      {
        status: 2,
        name: ''
      }]
  }),
  methods: {
    controlShowDivider(item, index) {
      item['showDivider'] = this.data.length > index + 1;
      return item;
    },
    change(item) {
      this.$data.disable = false;
      Http.searchTypeItem(this.$data, this.$props.search, {
        pageIndex:0
      },item)
    },
    load(){
      Http.searchTypeItem(this.$data, this.$props.search, {
        pageIndex:Math.floor(this.$data.data.length/10)
      },this.$props.status)
    }
  },
  props: {
    search: {
      type: String
    },
    status: {
      type: String
    },
    title: {
      type: String
    }
  },
  components: {
    XcProject
  }
}
</script>

<style>
.yb-type-more-project-container {
  background: #FFF;
}
</style>