<template>
  <v-container class="px-0 mt-0 pb-0">
    <el-form class="yb-project-add-form pb-3 pt-5 pr-5 rounded-md" ref="form" :model="form" label-width="80px">
      <el-form-item label="">
        <el-upload
            action="/image/upload"
            list-type="picture-card"
            :limit="5"
            with-credentials
            :on-success="uploadSuccess"
            :on-remove="remove"
        >
          <i class="el-icon-plus"></i>
        </el-upload>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.name"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.location"></el-input>
      </el-form-item>
      <el-form-item label="">
      <span class="yb-project-time"
            @click="startTimeVisible = true">{{ dateFormat('yyyy-MM-dd hh:mm', new Date(form.startTime)) }}</span>
        <span> - </span>
        <span class="yb-project-time"
              @click="endTimeVisible = true">{{ dateFormat("yyyy-MM-dd hh:mm", new Date(form.endTime)) }}</span>
        <nut-datepicker
            :is-visible="startTimeVisible"
            title=""
            type="datetime"
            :startDate="dateFormat('yyyy-MM-dd hh:mm',new Date())"
            endDate="2999-12-31 23:59"
            :defaultValue="dateFormat('yyyy-MM-dd hh:mm',new Date(form.startTime))"
            @close="startTimeVisible = false"
            @choose="setChooseStartTime"
        ></nut-datepicker>
        <nut-datepicker
            :is-visible="endTimeVisible"
            title=""
            type="datetime"
            :startDate="dateFormat('yyyy-MM-dd hh:mm',new Date(form.startTime))"
            endDate="2999-12-31 23:59"
            :defaultValue="dateFormat('yyyy-MM-dd hh:mm',new Date(form.endTime))"
            @close="endTimeVisible = false"
            @choose="setChooseEndTime"
        ></nut-datepicker>
      </el-form-item>
      <el-form-item label="">
        <el-input-number v-model="form.limitNum" :min="1" :max="99999"></el-input-number>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="typeName" @change="typeChange">
          <el-option v-for="(item) in type" :key="item.id" :label="item.name" :value="item.name"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" style="margin: 0">
      </el-form-item>
      <div id="editorContainer" class="yb-project-add-editor pt-2 px-2 ml-3 mt-2 mb-6"></div>
      <el-form-item>
        <el-button style="float: right" type="primary" icon="el-icon-check" circle @click="submit"></el-button>
      </el-form-item>
    </el-form>
  </v-container>
</template>

<script>
import {Form, FormItem, Input, Select, Button, Option, InputNumber, Upload, Message} from 'element-ui';
import NutUI from '@nutui/nutui-jdl';
import '@nutui/nutui-jdl/dist/nutui.css';
import timeUtils from "../utils/timeUtils";
import Vue from 'vue';
import 'zx-editor/dist/css/zx-editor.min.css'
import ZxEditor from 'zx-editor'
import Http from '@/repository/http';

Vue.use(Upload);
Vue.use(Form);
Vue.use(Input);
Vue.use(FormItem);
Vue.use(Button);
Vue.use(Select);
Vue.use(Option);
Vue.use(InputNumber);
NutUI.install(Vue);

export default {
  data: () => ({
    zxEditor: {},
    form: {
      name: "",
      type: null,
      location: "",
      startTime: new Date().getTime(),
      endTime: new Date().getTime(),
      limitNum: 40,
      viewSum: 0,
      banner: [],
      info: []
    },
    startTimeVisible: false,
    endTimeVisible: false,
    type: [],
    typeName: ""
  }),
  methods: {
    setChooseStartTime(time) {
      this.$data.form.startTime = new Date(time[5]).getTime();
      if (this.$data.form.startTime > this.$data.form.endTime) {
        this.$data.form.endTime = this.$data.form.startTime;
      }
    },
    setChooseEndTime(time) {
      this.$data.form.endTime = new Date(time[5]).getTime();
    },
    dateFormat(fmt, date) {
      return timeUtils.dateFormat(fmt, date)
    },
    typeChange(value) {
      this.$data.type.forEach(item => {
        if (item.name === value) {
          this.$data.form.type = item;
          console.log(this.$data.form.type);
          return;
        }
      })
    },
    submit() {
      this.$data.form.info = [];
      this.$data.form.banner.forEach(item => {
        delete item['file']
      });
      //info
      let html = new DOMParser().parseFromString(this.$data.zxEditor.getHtml(), "text/html");
      let itemList = html.getElementsByTagName("section");
      let info = this.$data.form.info;
      for (let i = 0; i < itemList.length; i++) {
        if (itemList[i].children.length === 1) {
          info.push({
            url: itemList[i].children[0].getAttribute("src"),
            content: itemList[i].innerText
          })
        } else {
          info.push({
            content: itemList[i].innerText
          })
        }
      }
      if (this.checkSubmit(this.$data.form)) {
        let _this = this;
        Http.createProjectItem(this.$data.form, Message, (data) => {
          Message.success(data.name + "");
          setTimeout(function () {
            _this.$router.go(-1);
          }, 2000);
        });
      }
    },
    checkSubmit(form) {
      //banner1
      if (form.banner.length === 0) {
        Message.error("1");
        return false;
      }
      if (form.name === "") {
        Message.error("")
        return false;
      }
      if (form.location === "") {
        Message.error("")
        return false;
      }
      if (form.startTime >= form.endTime) {
        Message.error("")
        return false
      }
      if (form.limitNum <= 0) {
        Message.error("1")
        return false
      }
      if (form.type == null) {
        Message.error("")
        return false
      }
      if (form.info.length === 0 || (form.info.length === 1 && form.info[0].content === "")) {
        Message.error("null")
        return false
      }
      return true;
    },
    uploadSuccess(response, file, fileList) {
      if (response.code === 200) {
        this.$data.form.banner.push({
          img: response.data.url,
          priority: 6 - fileList.length,
          file: file
        })
      }
    },
    remove(file) {
      let index = -1;
      for (let i = 0; i < this.$data.form.banner.length; i++) {
        if (this.$data.form.banner[i].file === file) {
          index = i;
          break;
        }
      }
      this.$data.form.banner.splice(index, 1);
    }
  },
  beforeMount() {
    Http.getTypeItem(this.$data);
  },
  mounted() {
    this.$data.zxEditor = new ZxEditor('#editorContainer', {
      placeholder: '',
      toolbarBtns: ['select-picture'],
      borderColor: '#DCDFE6',
      toolbarHeight: 56,
      customPictureHandler: true
    })
    let _this = this;
    this.$data.zxEditor.on('selectPictureInputChange', function (file) {
      Http.uploadImage(file,function (data){
          let img = new Image()
          img.src = data.url
          _this.$data.zxEditor.insertElm(img)
      })
    })
  }
}
</script>

