<template>
  <div class="ma-2 mx-0 yb-type-more-project-container">
    <v-tabs v-model="select" @change="change">
      <v-tab v-for="(i,index) in item" :key="index">
        {{ i.name }}
      </v-tab>
    </v-tabs>
    <XcProject v-for="(item,index) in data" :data="controlShowDivider(item, index)" :key="index"
               @onClick="()=>{$router.push('detail?title='+item.name+'&id='+item.id);}"/>
  </div>
</template>

<script>
import XcProject from "@/components/project";
import Http from '@/repository/http';

export default {
  name: "type_more",
  data: () => ({
    data: {},
    select: 1,
    item: [{
      status: 0,
      name: ''
    },
      {
        status: 1,
        name: ''
      },
      {
        status: 2,
        name: ''
      }]
  }),
  methods: {
    controlShowDivider(item, index) {
      item['showDivider'] = this.data.length > index + 1;
      return item;
    },
    change(item) {
      Http.getProjectItem(this.$data, this.$props.id, item)
    }
  },
  props: {
    id: {
      type: String
    },
    status: {
      type: String
    },
    title: {
      type: String
    }
  },
  components: {
    XcProject
  },
  beforeMount() {
    Http.getProjectItem(this.$data, this.$props.id, this.$props.status)
  }
}
</script>

<style>
.yb-type-more-project-container {
  background: #FFF;
}
</style>