<template>
  <div class="about">
    <user-info :user="user"></user-info>
    <div class="mt-4"></div>
    <expend-function v-for="(item,index) in expendData" :data="controlShowDivider(item, index)" :key="index"
                     @onClick="expendClick(item)">
    </expend-function>
    <div class="yb-float-add-project-button" v-if="addProjectShow" @click="()=>{this.$router.push('dialogPage/addProject?title=')}">
      <svg style="height: 56px;width: 56px" aria-hidden="true">
        <use xlink:href="#icon-shangchuan"></use>
      </svg>
      <div style="font-size: 14px;"></div>
    </div>
  </div>
</template>
<script>
import UserInfo from '../components/user_info.vue';
import ExpendFunction from '../components/expend_function.vue'
import Http from '../repository/http';
import {Message} from "element-ui";

export default {
  data: () => ({
    user: {},
    expendData: [],
    message:{
      type:"success",
      content:""
    },
    addProjectShow:false
  }),
  methods: {
    controlShowDivider(item, index) {
      item['showDivider'] = this.expendData.length > index + 1;
      return item;
    },
    expendClick(item) {
      item.onclick(this, (message)=>{
        Message.success(message)
      });
    }
  },
  components: {
    UserInfo,
    ExpendFunction
  },
  beforeMount() {
    Http.getUserInfo(this.$data);
  }
}
</script>

<style>
.yb-float-add-project-button {
  position: fixed;
  right: 25px;
  bottom: 80px;
}
.el-message {
  width: 80% !important;
  min-width: 80% !important;
  top: 2% !important;
}
</style>
