<template>
  <div class="pa-4 pb-100" style="opacity: 1; background-color: #FAFAFA">
    <div class="white rounded-lg">
      <v-carousel class="rounded-t-lg" :show-arrows="false" hide-delimiter-background
                  delimiter-icon="mdi-minus"
                  height="150px" cycle>
        <v-carousel-item v-for="(item, i) in detail.banner" :key="i" :src="item.img"/>
      </v-carousel>
      <v-container class="pa-5">
        <v-col class="rounded-b-lg">
          <v-row class="detail-name"><strong>{{ detail.name }}</strong></v-row>
          <v-row>
            <v-icon size="16">el-icon-location-outline</v-icon>
            <div class="detail-location ml-1 ">&nbsp;&nbsp;{{ detail.location }}</div>
          </v-row>
          <v-row>
            <v-icon size="16">el-icon-time</v-icon>
            <div class="detail-start-time ml-1 ">&nbsp;&nbsp;{{ convertTime(detail.startTime) }}</div>
          </v-row>
          <v-row class="mb-1">
            <v-icon size="16">el-icon-user</v-icon>
            <div class="detail-limit-num ml-1 ">&nbsp;&nbsp;{{ detail.currentNum }}/{{ detail.limitNum }}</div>
          </v-row>
          <v-row class="mt-5" justify="center">
            <v-btn class="rounded-lg fab-css" :style="{'background-color':color}" :disabled="disable"
                   @click="submit(detail.status)">
              {{ getStatus(detail.status) }}
            </v-btn>
          </v-row>
        </v-col>
      </v-container>
    </div>
    <v-container class="mt-3 pt-5 pb-3 white rounded-lg">
      <v-row>
        <v-icon class="ml-1" size="14">mdi-circle</v-icon>
        <strong class="ml-2"></strong>
      </v-row>
      <v-row>
        <div class="detail-divider"/>
      </v-row>

      <v-row v-for="(item, i) in detail.info" :key="i">
        <v-img v-if="item.url != null && item.url.trim().length > 0" class="yb-detail-img" contain max-width="100%"
               :src="item.url == null ? '' : item.url"/>
        <div v-if="item.content != null && item.content.trim().length > 0" class="ml-2">{{ item.content == null ? "" : item.content }}</div>
      </v-row>

      <v-row style="color: #909399">
        <v-col :cols="12">
          <div style="float: left">
            <v-icon size="15px" style="color: darkgrey">mdi-eye</v-icon>
            <div style="display: inline-block">&nbsp;{{ detail.viewSum }}</div>
          </div>
          <div style="float: right">
            <div>&nbsp;</div>
            <div>{{ convertDate(detail.createTime) }}</div>
          </div>
        </v-col>
      </v-row>
    </v-container>
    <div v-for="(item, index) in userList" :key="index">{{ item.ybUsernick }}</div>
    <CommentItems :data="comments" :project="detail"/>
  </div>


</template>

<script>
import CommentItems from '../components/comment_items.vue'
import Http from '@/repository/http'
import {Message} from 'element-ui'

export default {
  name: 'Detail',
  data: () => ({
    color: '#409EFF',
    detail: {},
    comments: [],
    disable: false,
    userList: []
  }),
  methods: {
    convertTime(time) {
      let date = new Date(time);
      let y = date.getFullYear();
      let m = date.getMonth() + 1;
      m = m < 10 ? '0' + m : m;
      let d = date.getDate();
      d = d < 10 ? ('0' + d) : d;
      return y + '-' + m + '-' + d;
    },
    convertDate(time) {
      let date = new Date(time);
      let m = date.getMonth() + 1;
      m = m < 10 ? '0' + m : m;
      let d = date.getDate();
      d = d < 10 ? ('0' + d) : d;
      let h = date.getHours() + 1;
      h = h < 10 ? '0' + h : h;
      let mm = date.getMinutes();
      mm = mm < 10 ? '0' + mm : mm;
      return m + '/' + d + " " + h + ':' + mm;
    },
    getStatus(status) {
      if (status === "-9223372036854775808" || status === "-9223372036854775807" || status === "-9223372036854775806") {
        //
        this.$data.color = "#E6A23C"
        return ""
      } else if ((status === "0" || status === "1" || status === "2"
          || status === "4" || status === "5" || status === "6"
          || status === "12" || status === "13" || status === "14") && this.$route.query.type === "check") {
        this.$data.color = "#409EFF"
        return ""
      } else if (status === "0") {
        //
        this.$data.color = "#409EFF"
        return ""
      } else if (status === "1") {
        //
        this.$data.color = "#E6A23C"
        return ""
      } else if (status === "2") {
        //
        this.$data.color = "#909399"
        this.$data.disable = true
        return ""
      } else if (status === "4" || status === "5" || status === "6") {
        //
        this.$data.color = "#F56C6C"
        if (status === "4") {
          this.$data.disable = true
        }
        if (status === "6") {
          this.$data.color = "#909399"
          this.$data.disable = true
        }
        return ""
      } else if (status === "12" || status === "13" || status === "14") {
        //
        this.$data.color = "#67C23A"
        return ""
      }
      return status;
    },
    submit(status) {
      if (status === "-9223372036854775808" || status === "-9223372036854775807" || status === "-9223372036854775806") {
        //
        Http.checkProject(this, this.detail, true, Message)
      } else if ((status === "0" || status === "1" || status === "2"
          || status === "4" || status === "5" || status === "6"
          || status === "12" || status === "13" || status === "14") && this.$route.query.type === "check") {
        //
        Http.checkProject(this, this.detail, false, Message)
      } else if (status === "0" || status === "1") {
        //
        Http.reportProject(this.$data, this.detail, true, Message)
      } else if (status === "5") {
        //
        Http.signProject(this.$data, this.detail, Message);
      }
    }
  },
  props: {
    type: {
      type: String
    }
  },
  components: {
    CommentItems,
  },
  beforeMount() {
    Http.getProjectItemDetail(this, this.$route.query.id);
  }
};
</script>


