<template>
  <v-container class="join pb-5">
    <v-tabs>
      <v-tabs v-model="select">
        <v-tab v-for="(i,index) in item" :key="index">
          {{ i.name }}
        </v-tab>
      </v-tabs>
    </v-tabs>
    <XcProject v-for="(item,index) in dataList" :data="controlShowDivider(item, index)" :key="index" type="default"
               @click="()=>{this.$router.push('dialogPage/detail&id='+item.id);}"/>
  </v-container>
</template>
<script>
import XcProject from "@/components/project";
import http from "@/repository/http";

export default {
  data: () => ({
    data: [],
    select: 1,
    item: [{
      status: 0,
      name: ''
    },
      {
        status: 1,
        name: ''
      },
      {
        status: 2,
        name: ''
      }]
  }),
  methods: {
    controlShowDivider(item, index) {
      item['showDivider'] = this.data.length > index + 1;
      return item;
    }
  },
  components: {
    XcProject
  },
  beforeMount() {
    console.log("mount");
    http.getJoinProjectItem(this.$data);
  },
  mounted(){
    console.log("mounted");
  },
  computed:{
    dataList(){
      let result = [];
      let time = new Date().getTime;
      this.$data.data.forEach(item => {
        if(this.$data.select === 0 && (time < (new Date(item.startTime).getTime()))){
          result.push(item)
        }else if(this.$data.select === 1 && time >= (new Date(item.startTime).getTime()) && time < (new Date(item.endTime).getTime)){
          result.push(item)
        }else if(this.$data.select === 1 && time >= (new Date(item.endTime).getTime)){
          result.push(item)
        }
      })
      return result
    }
  }
}
</script>

<style>
.join{
  background: #FFF;
}
</style>