<template>
  <ul class="infinite-list pb-4 pl-0 mt-2" v-infinite-scroll="load" style="overflow:auto" :infinite-scroll-disabled="disable" :infinite-scroll-distance="50" :infinite-scroll-delay="500">
    <XcProject v-for="(item,index) in data" :data="controlShowDivider(item, index)" :key="index"
               @onClick="()=>{$router.push('detail?title='+item.name+'&id='+item.id+'&type=my');}" type="my"/>
  </ul>
</template>

<script>
import XcProject from "@/components/project";
import {InfiniteScroll} from 'element-ui';
import Vue from 'vue';
import Http from '@/repository/http';

Vue.use(InfiniteScroll);

export default {
  data: () => ({
    data: [],
    disable: false
  }),
  methods: {
    load(){
      Http.selectProjectUnClick(this.$data, false);
    },
    controlShowDivider(item, index) {
      item['showDivider'] = this.data.length > index + 1;
      return item;
    }
  },
  components: {
    XcProject
  }
}
</script>

<style scoped>
.infinite-list{
  background: #FFF;

}
</style>