<template>

  <v-container>
    <v-container class="mt-3 white rounded-lg">
      <v-row>
        <v-icon class="ml-1" size="14">mdi-circle</v-icon>
        <strong class="ml-2"></strong>
      </v-row>
      <v-row class="mt-5">
        <Comment v-for="(item, i) in data" :data="controlShowDivider(item,i)" :key="i" :project="project"
                 :datas="data"/>
      </v-row>
      <div v-if="data.length === 0" style="text-align: center" class="my-6"></div>
    </v-container>
    <v-textarea
        class="mt-5 mb-5 white yb-detail-comment"
        auto-grow
        outlined
        rows="1"
        color="grey"
        label=""
        dense
        placeholder=""
        v-model="myComment">
      <div class="yb-comment-button rounded-lg py-2 px-3" @click="putComment" slot="append"></div>
    </v-textarea>
  </v-container>

</template>

<script>

import Comment from "@/components/comment.vue";
import Http from '@/repository/http';

export default {
  data: () => ({
    myComment: '',
  }),
  methods: {
    controlShowDivider(item, i) {
      item['showDivider'] = this.data.length > i + 1;
      return item;
    },
    putComment() {
      console.log(this.$data.myComment)
      Http.createComment(this.$props, this.$props.project, this.$data, {content: this.$data.myComment})
    }
  },
  props: {
    data: {
      type: Array
    },
    project: {
      type: Object
    }
  },
  components: {
    Comment,
  }

};
</script>

<style>
.yb-detail-comment .v-text-field__details{
  display: none !important;
}
.yb-detail-comment .v-input__slot{
  padding-bottom: 0 !important;
}
.yb-comment-button{
  position: absolute !important;
  bottom: 5px;
  right: 5px;
  background: #409EFF;
  color: #FFF;
  font-size: 14px;
}
</style>