<template>
  <div class="yb-banner-img pt-2">
    <el-upload
        action="/image/upload"
        list-type="picture-card"
        :limit="5"
        fit="cover"
        with-credentials
        :on-success="uploadSuccess"
        :on-remove="remove"
        :file-list="fileList"
    ><i class="el-icon-plus"></i></el-upload>
    <v-btn class="mt-7 yb-banner-img-button" color="#409EFF" dark elevation="0" rounded @click="submit"></v-btn>
  </div>
</template>

<script>
import {Message, Upload} from 'element-ui'
import Vue from 'vue'
import Http from '@/repository/http'

Vue.use(Upload)

export default {
  data: () => ({
    banners: [],
    fileList: []
  }),
  props: {
    url: {
      type: URL
    }
  },
  methods: {
    uploadSuccess(response, file, fileList) {
      if (response.code === 200) {
        this.$data.banners.push({
          img: response.data.url,
          priority: 6 - fileList.length,
          file: file
        })
      }
    },
    remove(file) {
      let index = -1;
      for (let i = 0; i < this.$data.banners.length; i++) {
        if (this.$data.banners[i].file === file) {
          index = i;
          break;
        }
      }
      this.$data.banners.splice(index, 1);
    },
    submit() {
      Http.updateBanner(this, Message, this.$data.banners)
    }
  },
  beforeMount() {
    let _this = this
    Http.getBanner(this.$data, (data) => {
      data.forEach(item => {
        _this.$data.fileList.push({name: item.id, url: item.img})
      })
    })
  }
}
</script>

<style>
.yb-banner-img {
  background: #FFF;
  text-align: center;
  display: block;
}

.el-upload-list {
  padding: 0 !important;
}

.el-upload--picture-card {
  width: 90% !important;
  margin-left: 5% !important;
  margin-right: 5% !important;
}

.el-upload-list__item {
  width: 90% !important;
  margin-left: 5% !important;
  margin-right: 5% !important;
}

.yb-banner-img-button {
  width: 90%;
}
</style>